// Input.tsx
import React from 'react';

import clsx from 'clsx';
import { focusRing } from '../../../tailwind/styles/focus';
import { InputProps } from './input.interface';
import { FunctionalIcon } from '../icon/Icon';

export const Input: React.FC<InputProps> = ({
  id,
  type,
  value,
  error,
  label,
  placeholder,
  disabled = false,
  icon,
  readOnly = false,
  hasInfoIcon = false,
  message,
  onIconToggle,
  onIconInfoClick,
  onChange,
  ...props
}) => {
  const searchCancel = clsx(
    'zep-search-cancel:zep-appearance-none',
    'zep-search-cancel:zep-w-1.5',
    'zep-search-cancel:zep-h-1.5',
    'zep-search-cancel:zep-border-[15px]',
    'zep-search-cancel:zep-border-solid',
    'zep-search-cancel:zep-border-transparent',
    'zep-search-cancel:zep-cursor-pointer',
    'zep-search-cancel:zep-bg-no-repeat',
    'zep-search-cancel:zep-mr-[-16px]',
    'zep-search-cancel:zep-bg-[url(/src/assets/search-cancel.svg)]',
  );

  const calendarPicker = clsx(
    'zep-calendar-picker:zep-appearance-none',
    'zep-calendar-picker:zep-w-1.5',
    'zep-calendar-picker:zep-h-1.5',
    'zep-calendar-picker:zep-p-0',
    'zep-calendar-picker:zep-border-x-[16px]',
    'zep-calendar-picker:zep-border-solid',
    'zep-calendar-picker:zep-border-transparent',
    'zep-calendar-picker:zep-cursor-pointer',
    'zep-calendar-picker:zep-bg-no-repeat',
    'zep-calendar-picker:zep-mr-[-16px]',
    'zep-calendar-picker:zep-bg-[url(/src/assets/calendar.svg)]',
  );

  return (
    <span
      className={clsx(
        'zep-inline-flex',
        'zep-flex-col',
        'zep-flex-wrap',
        'zep-items-stretch',
        'zep-justify-start',
        'zep-relative',
        [disabled ? 'zep-cursor-not-allowed zep-opacity-disabled' : 'zep-cursor-pointer'],
        props.className,
      )}
      data-testid="zep-input"
    >
      {label && (
        <label htmlFor={id} className={clsx('zep-mb-0.5', 'zep-text-neutral-dark-default')}>
          {label}
          {!props.required && <span className="zep-ml-0.25">(optional)</span>}
        </label>
      )}
      <div className={clsx('zep-inline-flex', 'zep-flex-row', 'zep-flex-nowrap', 'zep-items-center', 'zep-gap-0.5')}>
        <div className={clsx('zep-relative', 'zep-w-full')}>
          <input
            id={id}
            value={value}
            defaultValue={''}
            placeholder={placeholder}
            type={type}
            onChange={!disabled ? onChange : undefined}
            aria-describedby={`aria-describedby-${id}`}
            disabled={disabled}
            data-testid="zep-input-field"
            {...props}
            className={clsx(
              focusRing,
              'zep-border-solid',
              'zep-border-1',
              'zep-w-full',
              'zep-rounded-4',
              'zep-h-3',
              'zep-px-1',
              'zep-leading-[48px]',
              'zep-leading-[22px]',
              'focus-visible:zep-ring-offset-1',
              'invalid:[&:not(:placeholder-shown):not(:focus)]:zep-border-error',
              'invalid:[&:not(:placeholder-shown):not(:focus)]:zep-border-2',
              { 'zep-border-neutral-dark-default': !error },
              { 'zep-border-2 zep-border-error': error },
              {
                'zep-cursor-not-allowed zep-bg-background-medium': readOnly, // does not work at the moment
              },
              { [calendarPicker]: type === 'date' },
              { [searchCancel]: type === 'search' },
            )}
          />
          {icon && (
            <FunctionalIcon
              onClick={onIconToggle}
              className={clsx('zep-absolute', 'zep-top-[0px]', 'zep-bottom-0', 'zep-right-1', 'zep-h-full', {
                'zep-cursor-pointer': onIconToggle,
              })}
              name={icon}
            />
          )}
        </div>
        {hasInfoIcon && <FunctionalIcon onClick={onIconInfoClick} name="info-circle-filled" />}
      </div>
      {message && (
        <div
          className={clsx(
            'zep-inline-flex',
            'zep-flex-row',
            'zep-items-center',
            'zep-gap-0.25',
            'zep-text-error',
            'zep-mt-0.25',
          )}
        >
          <FunctionalIcon name="warning-triangle-filled" />
          <span id={`aria-describedby-${id}`}>{message}</span>
        </div>
      )}
    </span>
  );
};
